import { Link } from "react-router-dom";

function About(){
    
    return(
        <>
            <div className="row">
                <div className="col-5" data-aos="fade-up-right" data-aos-offset="100" data-aos-duration="1000" data-aos-easing="ease-in-out">
                    <img src="chef.png"/>
                </div>

                <div className="col-7" data-aos="fade-up-left" data-aos-duration="1000"><br/>
                    <h3 style={{color:"orange",marginTop:"20px"}}><b>Awesome food and super home delivery</b></h3>
                    <p> Looks can be deceptive and Deepanshu Restaurants is a fine example of this proverb. 
                        Located in the Shaktikhand 1st area of Indirapuram, it can easily be passed on as 
                        any other small time roadside restaurant. It provides some really amazing food in 
                        quite affordable prices and their home delivery is really prompt. They are really
                         popular all around Indirapuram.</p>
                         
                    <p>Pros:<br/>
                    1. Location - Easily located in the Shaktikhand 2 area on the main road<br/>
                    2. Ambiance - Simple seating area nothing fancy (its their home delivery that is more famous)<br/>
                    3. Food - Awesome food whether it is veg or non-veg items<br/>
                    4. Price - Very budget friendly priced, sometimes lower than some more popular restaurants without compromising on quality<br/>
                    5. Delivery - Prompt home delivery which will easily fail the Pizza guys at any time</p><br/>     

                    <Link to="/feedback" className="form-control btn btn-outline-warning" style={{color:"white"}} type="button">Share Your Feedback</Link><br/><br/>
                    </div>
            </div>
             
        </>
    )
}
export default About;