import { Link } from "react-router-dom";


function Navbar(){
    return(
        <>
        <div className="navbar">
            <h3><img src="chef.png" width={'70px'}/>&nbsp;<div className="text-clr" style={{display:"inline"}}><b>Your</b></div><b>City</b></h3>
        <div className="navbar_container">
        <Link to="/"  style={{marginLeft:"30px",textDecoration:"none",color:"black", paddingRight:"10px",fontWeight:"500"}}>Home</Link>
        <Link to="menu" style={{marginLeft:"30px",textDecoration:"none",color:"black",paddingRight:"10px",fontWeight:"500"}}>Menu</Link>
        <Link to="about"   style={{marginLeft:"30px",textDecoration:"none",color:"black",paddingRight:"10px",fontWeight:"500"}}>About</Link>
        <Link to="feedback" style={{marginLeft:"30px",textDecoration:"none",color:"black",paddingRight:"15px",fontWeight:"500"}}>Feedback</Link>
        </div>
        </div>
        </>
    )
}
export default Navbar;