import React, { useEffect, useState } from 'react'
import axios from 'axios'
const Menu = () => {
  const [items,setItems]=useState([])

  const data= async()=>{
    const url= "https://api.spoonacular.com/recipes/random?number=30&tags=vegetarian,dessert&apiKey=97a177ce74044dfd8b0e42971be60549"
    const res = await axios.get(url)
    setItems(res.data.recipes)
    console.log(res.data.recipes);
}
useEffect(()=>{
  data()
},[])

  return (
    <div className='container'>
    <div className='menu'>
       <div className="row">
            <h4 style={{fontFamily:"cursive"}}><b>Popular</b></h4>
                <div className="col-3"> 
                    <div className="popular_main" style={{background:"white",padding:"10px"}}>
                        <img src="cak.jpg" width={"100%"} height={"135px"}/>
                        <p style={{background:"white"}}>Favourite Moist Chocolate Cake</p>
                    </div>
                </div>

                <div className="col-3"> 
                    <div className="popular_main" style={{background:"white",padding:"10px"}}>
                        <img src="chicken.jpg" width={"100%"} height={"135px"}/>
                        <p style={{background:"white"}}>Ginger soy chicken</p>
                    </div>
                </div>

                <div className="col-3"> 
                    <div className="popular_main" style={{background:"white",padding:"10px"}}>
                        <img src="cho.jpg" width={"100%"} height={"135px"}/>
                        <p style={{background:"white"}}>Homemade Chocolates</p>
                    </div>
                </div>

                <div className="col-3"> 
                    <div className="popular_main" style={{background:"white",padding:"10px"}}>
                        <img src="egg.jpg" width={"100%"} height={"135px"}/>
                        <p style={{background:"white"}}>Chettinad Egg curvy</p>
                    </div>
                </div>
            </div><br/>

      <div className='menu_card'>
        {
          items.map((item , index)=>{
            return(
              <div className='menu_body' key={item.id}>
                <div className='card'>
                <img src={item.image} width={100} />
                <div className='card-title'>
                  <div className='text-center'>
                  <h6>{item.title}</h6>
                    <p>{item.dishTypes}</p>
                      <h6>${item.pricePerServing}</h6>
                      <center>
                      <div><center><h3 className='btn btn-warning' style={{width:"250px",marginRight:"50px"}}>Details</h3></center></div>
                  </center>
                  </div>
                  </div>
                </div>
                </div>
              
            )
          })
        }
      </div>
    </div>
    </div>
  )
}

export default Menu;
