import { useState } from "react";

function Feedback(){
    const [name,setName]=useState("");
    const [email,setEmail]=useState("");
    const [feedback,setFeedback]=useState("");

    function onSub(){
        alert("Name:"+name)
        alert("Email:"+email)
        alert("Feedback:"+feedback)

    }

    return(
        <>
        <br/>
        <div className="container" data-aos="zoom-in" data-aos-offset="100" data-aos-duration="1000" data-aos-easing="ease-in-sine">
            <h3 style={{color:"orange"}}>Happy Happy!</h3><br/>
            <form onSubmit={onSub}>
                <input type="text" className="form-control" name="name" placeholder="Enter your name" value={name} onChange={(e)=> setName(e.target.value)}/><br/>
                <input type="email" className="form-control" name="email" placeholder="Enter your email" value={email} onChange={(e)=> setEmail(e.target.value)}/><br/>
                <textarea type="text" className="form-control" rows={5} cols={10} name="feedback" placeholder="Enter your feedback" value={feedback} onChange={(e)=> setFeedback(e.target.value)}/><br/>
                <button type="submit" className="form-control btn btn-outline-warning" style={{padding:"10px",color:"white"}}>Submit</button>



            </form>
        </div>
        </>
    )
}
export default Feedback;